@import '../../var';
@import '../../common';

@back-body-image: inline('images/bg.png');
@back-body-position: bottom;
@back-body: @back-body-image @back-body-position no-repeat;
@back-size-body: 100% auto;
.wrapper {
    background: rgb(255, 255, 255) @back-body;
    background-size: @back-size-body;

    .landscape-block({
        background-size: cover;
        background-position: center 90%;
    });
    .xxs-h(@lg, @p, {
        background-position: top;
    });

}

/* header */
@back-header-image: rgb(255, 255, 255);
@back-header-position: center;
@back-header: @back-header-image @back-header-position no-repeat;
@back-size-header: 100% 100%;
@box-shadow-header: 0px 3px 29px 0px rgba(0, 0, 0, 0.15);
@border-header: none;

.header {
    background: @back-header;
    background-size: @back-size-header;

    box-shadow: @box-shadow-header;
    border-bottom: @border-header;

    .flex;
    .justify-between;
    .items-center;
}


@back-header-logo-image: inline('images/logo.png');
@back-header-logo-position: left;
@back-header-logo: @back-header-logo-image @back-header-logo-position no-repeat;
@back-size-header-logo: contain;

@width-header-logo: 60%;
@height-header-logo: 60%;

@margin-header-logo: 4%;

.header__logo {
    background: @back-header-logo;
    background-size: @back-size-header-logo;

    width: @width-header-logo;
    height: @height-header-logo;

    margin-left: @margin-header-logo;
}

@back-header-elements-image: inline('images/18.png');
@back-header-elements-position: right;
@back-header-elements: @back-header-elements-image @back-header-elements-position no-repeat;
@back-size-header-element: contain;

@width-header-elements: 80% - @width-header-logo;
@height-header-elements: 35%;

@margin-header-elements: 4%;

.header__elements {
    background: @back-header-elements;
    background-size: @back-size-header-element;

    width: @width-header-elements;
    height: @height-header-elements;

    margin-right: @margin-header-elements;
}


@back-header-extra-image: rgba(255, 255, 255, .8);
@back-header-extra-position: center;
@back-header-extra: @back-header-extra-image @back-header-extra-position no-repeat;
@back-size-header-extra: contain;
@height-header-extra: 50px;
@margin-top-header-extra: 80px;
@margin-bottom-header-extra: @margin-top-header-extra * -1;

.header__extra {
    position: relative;
    display: none;
    z-index: 1;

    width: 86%;
    //min-height: @height-header-extra;

    margin: @margin-top-header-extra auto @margin-bottom-header-extra + 10px;

    background: @back-header-extra;
    background-size: @back-size-header-extra;

    .tiny-h(@xsm, @p, { // 5
        margin-top: 145px;
        margin-bottom: -135px;
    });

    .xxs-h(@md, @p, { // 6+
        margin-top: 175px;
        margin-bottom: -195px;
    });

    .sm-h(@lg, @l, { //  
        display: block;
        margin-bottom: -170px;
    });

    background: @back-header-extra;
    background-size: @back-size-header-extra;

    .tiny-h(@xs, @p, {
        display: block;
    })
}

.relation {
    height: 0;
    .ratio( 8, 1 );

}

@back-header-relation-image: inline('images/header-extra.png');
@back-header-relation-position: center;
@back-header-relation: @back-header-relation-image @back-header-relation-position no-repeat;
@back-size-header-relation: 100% auto;

.relation__content {
    .full-size;
    position: absolute;
    top: 0; left: 0;

    background: @back-header-relation;
    background-size: @back-size-header-relation;

    .xsm( background-size, 65% auto )

}

/* button */
@back-button-buy-image: rgb(230, 43, 60);
@back-button-buy-position: center;
@back-button-buy: @back-button-buy-image @back-button-buy-position no-repeat;
@back-size-button-buy: 100% 100%;
@border-button-buy: none;
@border-r-button-buy: 50px;
@color-button-buy: #fff;
@box-shadow-button-buy: none;

.button__buy {
    background: @back-button-buy;
    background-size: @back-size-button-buy;
    z-index: 10;

    border: none;
    //box-sizing: border-box;
    //border-bottom: @border-button-buy;
    border-radius: @border-r-button-buy;
    color: @color-button-buy;

    box-shadow: @box-shadow-button-buy;

    font-size: 3rem;
    .xs( font-size, 3.1rem )
}

